<template>
  <el-container>
    <el-header style="padding: 0">
      <el-menu :default-active="activeIndex"
               class="el-menu-demo"
               mode="horizontal"
               @select="handleSelect"
               background-color="#545c64"
               text-color="#fff"
               active-text-color="#ffd04b">
        <el-menu-item index="FileBrowser">文件浏览</el-menu-item>
        <el-menu-item index="ConfigManage">配置管理</el-menu-item>
      </el-menu>
    </el-header>
    <el-main>
      <router-view />
    </el-main>
  </el-container>

</template>
<script>
export default {
  data () {
    return {
      activeIndex: 'FileBrowser',
    };
  },
  methods: {
    handleSelect (key, keyPath) {
      console.log(key, keyPath);
      this.$router.push('/' + key)
    }
  }
}
</script>


