<template>
  <el-container>
    <el-table :data="files"
              style="width: 100%">
      <el-table-column prop="name"
                       label="文件名"
                       width="180">
      </el-table-column>
      <el-table-column prop="fileType"
                       label="文件类型"
                       width="180">
      </el-table-column>
      <el-table-column prop="size"
                       label="文件大小">
      </el-table-column>
      <el-table-column prop="accessUrl"
                       label="下载地址">
      </el-table-column>
    </el-table>
  </el-container>
</template>
<script>
import request from '../utils/request'

export default {
  data () {
    return {
      files: []
    }
  },
  created () {
    request({ method: 'get', url: 'api/listResource' }).then(res => this.files = res.context)
  },
}
</script>


