import Vue from "vue";
import Router from "vue-router";
import Home from "@/pages/Home";
import FileBrowser from "@/components/FileBrowser";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "Home",
      component: Home,
      children: [
        {
          path: "/",
          name: "FileBrowser",
          component: FileBrowser
        }
      ]
    }
  ]
});



